import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import { routes } from './routes';
import VueResource from 'vue-resource';

Vue.use(VueRouter);
const router = new VueRouter({
	routes: routes,
	mode: 'history'
});

Vue.use(VueResource);

new Vue({
  render: h => h(App),
  router: router,
}).$mount('#app')
