<template><!--TODO:Add Nice Font-Family to Table-->
    <main>
        <form action="index.html" method="post">
            <table>
                <thead>
                    <tr>
                        <th>Item Name</th>
                        <th>Quantity (in Carton)</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, index) in items" :key="index">
                        <td style="text-align:left;"> {{ item.name }} </td>
                        <td style="text-align:center;"> {{ item.quantity }} </td>
                        <td><button :class="{disabled: item.quantity>=100}" type="button" :disabled="item.quantity>100" @click="item.quantity++"><strong>+</strong></button><button :class="{disabled: item.quantity<=0}" type="button" :disabled="item.quantity<=0" @click="item.quantity--"><strong>-</strong></button></td>
                        <td></td>
                    </tr>
                    <tr>
                        <td style="text-align:right;"> Total :</td>
                        <td merge>{{ total }}</td>
                        <td> <strong><input type="submit" class="submit" value="SUBMIT" :class="{disabled: total<1}" :disabled="total<1"></strong> </td>
                    </tr>
                    <tr>
                        <input type="radio" name="orderType" v-model="orderType" value="State">State
                        <input type="radio" name="orderType" v-model="orderType" value="Inter-State">Inter-State
                    </tr>
                </tbody>
                <select name="orderOpt" v-model="orderOpt" placeholder="Select Any Option">
                    <option value="Deliver">Deliver It</option>
                    <option value="Pickup">Pickup By Myself</option>
                </select>
            </table>
        </form>
    </main>
</template>

<script>
    export default {
        name: 'order',
        data() {
          return{
            orderOpt:"Deliver",
            orderType:"State",
            items:[
            {
              name:"Doritos Nacho Cheese",
              quantity:0,
              },
            {
              name:"Kurkure Hyderbadi Hungama",
              quantity:0,
              },
            {
              name:"Kurkure Green Chutney",
              quantity:0,
              },
            {
              name:"Kurkure Masala Munch",
              quantity:0,
              },
            {
              name:"Kurkure Naughty Tomato",
              quantity:0,
              },
            {
              name:"Kurkure Solid Masti",
              quantity:0,
              },
            {
              name:"Lay's Chile LimÓn",
              quantity:0,
              },
            {
              name:"Lay's Classic Salted",
              quantity:0,
              },
            {
              name:"Lay's India's Magic Masala",
              quantity:0,
              },
            {
              name:"Lay's Spanish Tangy Tomato",
              quantity:0,
              },
            ]
          }
        },
        computed: {
            total(){
                let sum = 0;
                for (let i = 0; i < 10; i++) {
                    sum+=parseInt(this.items[i].quantity);
                }
                return sum;
            },
        },
        methods: {
            submit() {
                return this.data();
            }
        }
    };
</script>
<style scoped>
    table {
        position: relative;
        margin: auto;
        margin-top: 3vh;
        text-align: center;
        background: white;
        height: auto;
        border: 0.5px black solid;
    }
    tr {
        border: 0.5px black solid;
        background: lightgray;
    }
    button {
        background-color: #b39cd0;
        border: none;
        box-shadow: 5px 5px 14px 1px rgba(00,00,00,0.1);
        text-align: center;
        width: 30px;
        height: 15px;
        border-radius: 40px;
    }
    button>strong {
        padding: auto;
    }
    .disabled {
        opacity: 0.5;
    }
    .submit {
        width: 130px;
        height: 40px;
        background-color: #b39cd0;
        border-radius: 35px;
        box-shadow: 8px 8px 14px 1px rgba(00,00,00,0.1);
        border: 1px solid white;
        text-align: center;
        cursor: pointer;
        padding: auto;
    }
    @media screen and (min-width: 720px) {
        table {
            font-size: 20px;
        }
        button {
            cursor: pointer;
            font-size: 20px;
            height: 20px;
            width: 40px;
            margin: auto;
            transition: all 1.5s;
        }
        button:active {
            box-shadow: none;
        }
    }
</style>
